// add this to theme color functions
// https://getbootstrap.com/docs/4.0/getting-started/theming/#functions
$primary: map-get($theme-colors, 'primary');
$enable-rounded: false;
$enable-deprecation-messages: true;
$enable-important-utilities: false;

$body-bg: map-get($theme-colors, 'bodybg');
$body-color: map-get($theme-colors, 'text');
$grid-gutter-width: 30px;

$font-size-base: 1rem;

$font-family-sans-serif: 'panton', sans-serif;
$headings-font-family: 'panton', sans-serif;
$font-weight-lighter: 400;
$font-weight-light: 400;
$font-weight-normal: 400;
$font-weight-bold: 700;
$font-weight-bolder: 700;
$headings-font-weight: 400;

$font-family-brandheader: 'panton', sans-serif;
$font-weight-brandheader: 900;
$font-size-brandheader-xs: 40px;
$font-size-brandheader-sm: 68px;
$font-size-brandheader-md: 92px;
$font-size-brandheader-lg: 125px;
$font-size-brandheader-xl: 149px;
$font-size-brandheader-xxl: 172px;

// fonts
$dark: map-get($theme-colors, 'text');
$blockquote-font-size: $font-size-base;
$link-color: darken($primary, 30%);
$link-hover-color: tint-color($link-color, 30%);

// tables
$table-border-color: map-get($theme-colors, 'bodybg');

// cards
$card-cap-bg: rgba($primary, 0.3);
$card-bg: shade-color(map-get($theme-colors, 'contentbg'), 3%);

// carousel
$carousel-control-width: 15%;
$carousel-control-opacity: 0.5;
$carousel-indicator-width: 20px;
$carousel-indicator-height: 9px;
$carousel-indicator-spacer: 3px;
$carousel-indicator-active-bg: map-get($theme-colors, 'primary');
$carousel-caption-width: 70%;
$carousel-caption-color: map-get($theme-colors, 'primary');
$carousel-control-icon-width: 20px;

// pagination
$pagination-color: tint-color(map-get($theme-colors, 'contentbg'), 20%);
$pagination-bg: darken($primary, 30%);
$pagination-border-color: rgba(map-get($theme-colors, 'text'), 0.2);
$pagination-hover-color: map-get($theme-colors, 'primary');
$pagination-hover-bg: tint-color(map-get($theme-colors, 'contentbg'), 20%);
$pagination-hover-border-color: rgba(map-get($theme-colors, 'text'), 0.2);
$pagination-active-color: tint-color(map-get($theme-colors, 'contentbg'), 20%);
$pagination-active-bg: darken($primary, 30%);
$pagination-active-border-color: rgba(map-get($theme-colors, 'text'), 0.2);
$pagination-disabled-bg: darken($primary, 30%);
$pagination-disabled-border-color: rgba(map-get($theme-colors, 'text'), 0.2);
$pagination-disabled-color: tint-color(map-get($theme-colors, 'contentbg'), 40%);

// progress bars
$progress-bg: map-get($theme-colors, 'contentbg');
$progress-bar-color: map-get($theme-colors, 'text');
$progress-bar-bg: map-get($theme-colors, 'primary');
$progress-height: 5px;

// list groups
$list-group-bg: transparent;

// brandcolors
$brandcolors: (
	'facebook': #3b5998,
	'messenger': #0084ff,
	'twitter': #1da1f2,
	'whatsapp': #25d366,
	'snapchat': #fffc00
);

$header-image: 'header-201906.jpg';

// forms
$input-color: map-get($theme-colors, 'bodybg');
$input-border-color: map-get($theme-colors, 'bodybg');
$input-bg: map-get($theme-colors, 'contentbg');
$input-placeholder-color: map-get($theme-colors, 'bodybg');
